/*

 Header tag <#main-header>
 Wrapper <.header-holder>

 Logo  <.logo-container-x1 >
 Nav Desktop and mobile wrapper <"#et-top-navigation "><nav #nav>

 Nav Desktop <ul #top-menu>
 Nav Mobile  <.menu-mobile> <ul #top-menu>

 Hamburger mobile icon <#et_mobile_nav_menu><span .hamburger>
*/



#main-header .header-holder{
  position: relative;
  height: 160px !important;
  width: 85%;
  .nav-col-left,
   #et-top-navigation.nav-col-right{
   position: absolute;
  }
  .nav-col-left{ left:0; bottom: 20px; }
  #et-top-navigation.nav-col-right{ right: 0; bottom:-10px }
}

/*Nav col left elements*/

.nav-col-left a img{
  float: left;
  width: 70%;
}




#main-header{
  position: relative;
  background: #fff;
  z-index: 3;
  .header-holder {
    position: relative;
    margin: 0 auto;
    max-width: none;
    padding: 0;
  }
  // Social Icons
  .et-social-icon a{
    color:rgb(163, 153, 180)!important;
    font-size: 15px !important;
  }
  .et-social-icons{
    float:none !important;
  }
  .menu-mobile{display: none;}
}

// Navbar - Mobile and Desktop
#et-top-navigation{
  text-align:right !important;
}
