@media screen and (min-width: 201px) and (max-width: 823px){
  .bottom-1,
  .bottom-2,
  .bottom-3,
  .bottom-4,
  .bottom-5,
  .bottom-6,
  .bottom-7,
  .bottom-8,
  .grid-line.right,
  .grid-line.second,
  .grid-line.bottom {
    display: none;
  }
  .content-box{
    width:100%;
    margin-bottom: 1em;
    height: auto;
  }
  .content-box span{
    bottom:0;
  }
}


